<template>
  <div>
    <div class="mhead">
      <ul>
        <li>
          <text>自助缴费机</text>
        </li>
      </ul>
    </div>
    <div class="body" style="background: #f0f0f0;padding: 20%;">
      <!-- 卡表缴费 -->
      <div @click='sale("card")' class="auto" style="margin-top: 10%;" v-if="hasCardSale">
        <img src="./image/cardSale.png" alt="" class="img-responsive" />
      </div>
      <!-- 无卡收费 -->
      <div @click='sale("machine")' class="auto" style="margin-top: 10%; " v-if="hasMachine">
        <img src="./image/machineSale.png" alt="" class="img-responsive" />
      </div>
      <!-- 主页二维码展示-->
      <div class="auto" style="margin-top: 10%;" v-show="isShowCode">
        <img :src="imagePath" height="70%" width="70%" style="margin: 0 auto;" class="img-responsive"  style="display: none !important;"/>
        <h5 style="margin-left: 15%;color: #000000">{{tips}}</h5>
       </div>
    </div>
    <div class="foot" v-if="hasCardSale">
      <text >↓↓↓底部插入您的燃气卡↓↓↓</text>
    </div>
  </div>
</template>
<script>

import Vue from 'vue'
export default {
  title: '收费导航页',
  name: 'Main',
  data () {
    return {
      Sn: '',
      hasCardSale: false,
      hasMachine:false,
      isShowCode: false,
      tips: '',
      imagePath: ''
    }
  },
  methods: {
    callBack(msg) {
      if (msg === 'tomain') {
        return
      }
      if (msg !== undefined && msg !== null && msg !== '') {
        this.$showMessage(msg)
      }
    },
    sale (type) {
      if (type === 'card') {
        this.$goto('card-main', {}, 'self', this.callBack)
      }else {
        this.$goto('machine-main', {}, 'self', this.callBack)
      }
    }
  },
  ready () {
    // 设备序列号
    this.Sn = this.$PosUtil.Sn
    // 是否卡表缴费和无卡收费
    this.hasCardSale = this.$PosUtil.posParam.hasCardSale
    this.hasMachine = this.$PosUtil.posParam.hasMachine
    // 是否显示二维码和小提示
    if (this.$PosUtil.posParam.isShowCode) {
      this.isShowCode = true
      this.tips = this.$PosUtil.posParam.tips
      this.imagePath = Vue.ProxyUrl + '/res/' + this.$PosUtil.posParam.codeImagePath + '/code.jpg'
    }
  }
}
</script>
<style>

</style>



